import { invoke } from '@kds/bridge-lite';
/**
 * 获取 app 的一般信息
 * https://kds.corp.kuaishou.com/kds-react/api/000-BridgeCenterAPIS.html?platform=krn&namespace=system&name=getAppInfo
 */

export default async () => {
    try {
        const result = await invoke('system.getAppInfo', {});
        return result;
    } catch (err) {
        console.log(err);
        return {
            kpn: '',
            did: '',
            kpf: '',
        };
    }
};